import { RouterModule, Routes } from "@angular/router";
import { NgModule } from "@angular/core";
import { AuthGuard } from "../../auth/auth.gards";
import { SecurityManagmentComponent } from "./Security-managment.component";
import { AddUserComponent } from "./add-User/add-user.component";
import { EditUserComponent } from "./edit-User/edit-user.component";
import { ConsultUserComponent } from "./consult-User/consult-user.component";
import { NbAuthComponent } from "@nebular/auth";

const routes: Routes = [{
    path: '',
    component: SecurityManagmentComponent,
    children: [
      {
        path: 'addUser',
        component: AddUserComponent,canActivate: [AuthGuard]
      },
      {
        path: 'editUser/:id',
        component: EditUserComponent,canActivate: [AuthGuard]
      },
     
     
      {
        path: 'Users-table',
        component: ConsultUserComponent,canActivate: [AuthGuard]   },
    
  ]}];
  
  @NgModule({
    imports: [
      RouterModule.forChild(routes),
    ],
    exports: [
      RouterModule
    ],
  })
  export class SecurityManagmentRoutingModule {
  
  }
  
  export const routedComponents = [
    SecurityManagmentComponent,
    AddUserComponent,
    EditUserComponent,
    ConsultUserComponent
  ];
  