import { Subscription } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { NbDateService, NbThemeService, NbDialogService } from "@nebular/theme";
import { Component } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { AuthenticationService } from "../../../auth/Auth.Service";
import { ShowcaseDialogComponent } from "../../modal-overlays/dialog/showcase-dialog/showcase-dialog.component";
import { DomSanitizer } from "@angular/platform-browser";



@Component({
  selector: 'ngx-edit-User',
  styleUrls: ['./edit-user.component.scss'],
  templateUrl: './edit-user.component.html',
})
export class EditUserComponent {
  EditUserForme: FormGroup;
  id:string;
  usenameMessages:any;

  cofirmedPass:boolean=false;
  cofirmePass:boolean=true;
  userName: any;
  password: any;
  role: any;
    constructor(private router:Router,private http:HttpClient,private fb: FormBuilder,private authService:AuthenticationService,private dialogService:NbDialogService,private route: ActivatedRoute,private sanitizer:DomSanitizer ){ 
      this.route.params.subscribe(params=>{this.id=params['id'];
      this.http.get<any>('http://localhost:3000/rfq/users/'+this.id).subscribe(result=>{
        this.EditUserForme.get('userName').setValue(result.userName);
        this.EditUserForme.get('password').setValue(result.password);
        this.EditUserForme.get('role').setValue(result.role);


    
    });
  });
  this.sanitizer.bypassSecurityTrustHtml("SafeValue");
 }
    ngOnInit(): void {
    

      this.EditUserForme = this.fb.group({
        userName: ['', Validators.required ],
        password: ['', Validators.required ],
        cofirmpassword: ['', Validators.required ],
        role: ['', Validators.required ]
      });
      this.onChanges()
    
  }
  EditUser(userData){
  
  if(this.EditUserForme.get('password').value==this.EditUserForme.get('cofirmpassword').value){
  
  
  
  
     this.http.put<any>('http://localhost:3000/rfq/users/'+this.id,userData).subscribe(result=>{
      if(result.message==null){ this.router.navigate(['pages/Security-managment-elements/Users-table']);
      this.dialogService.open(ShowcaseDialogComponent, {
        context: {
          
          message:'User Updated successfully ',
        },
      });
    }
    else if (result.message!=null)

    {this.usenameMessages=result.message;}
     });
  
   }
   
   
  
  
  }
  
  onChanges():void{
    this.EditUserForme.get('userName').valueChanges.subscribe(val => {
      this.usenameMessages = null;
    });
    this.EditUserForme.get('cofirmpassword').valueChanges.subscribe(val => {
      if(this.EditUserForme.get('password').value!=this.EditUserForme.get('cofirmpassword').value){
        this.cofirmePass=false; 
        this.cofirmedPass=false;  
      }
      else {this.cofirmedPass=true;
      this.cofirmePass=true;}
    });
  }
  
  }
  
  
  