import { Component } from "@angular/core";
import { LocalDataSource } from "ng2-smart-table";
import { HttpClient } from "@angular/common/http";
import { Router } from "@angular/router";
import { AuthenticationService } from "../../../auth/Auth.Service";
import { NbDialogService, NbThemeService } from "@nebular/theme";
import { ShowcaseDialogComponent } from "../../modal-overlays/dialog/showcase-dialog/showcase-dialog.component";
import { Subscription } from "rxjs";

@Component({
    selector: 'ngx-User-table',
    templateUrl: './consult-user.component.html',
    styleUrls: ['./consult-user.component.scss'],
  })
  export class ConsultUserComponent  {
    themeName = 'default';
    Settings: Array<any>;
    themeSubscription: Subscription;
    
  
    settings = {
      
      actions:{
        add: false,
      edit:false,
  
  custom:[
   
  {
    name:'editroute',title:` <i  class="nb-edit"  [nbPopover]="icon"></i>`
  },
  
  ],position: 'right'
      },  delete: {
        deleteButtonContent: '<i class="nb-trash"></i>',
        confirmDelete: true,
      },
     
     
      
      columns: {
        userName: {
          title: 'UserName',
          type: 'string', addable: false,
        },
        password:{
        title: 'Password',
        type: 'string', addable: false,
       },
       role:{
        title: 'Role',
        type: 'string', addable: false,
       }
       
       
      },
    };
 
    source: LocalDataSource;
   
  
    constructor(private http:HttpClient,private router:Router,private authserv:AuthenticationService,private dialogService:NbDialogService,private themeService: NbThemeService) {
     if(this.authserv.Role['subject']=='admin'){
        this.http.get<any>('http://localhost:3000/rfq/users').subscribe(result=>{
            this.source=new LocalDataSource(result);

   
       
      }
      
      
      );  this.themeSubscription = this.themeService.getJsTheme().subscribe(theme => {
        this.themeName = theme.name;
        this.init(theme.variables);
        
      });  }
         
         else{ this.dialogService.open(ShowcaseDialogComponent, {
          context: {
            title: 'User not permetted',
            message:'this section is not a part of your role !!',
            
          },
        });
      this.router.navigate(['pages']);}}
         editUser(event){
          switch ( event.action) {
            case 'editroute':
            {  
              this.router.navigate(['pages/Security-managment-elements/editUser',event.data._id]);
            
          }
            break;
     
          
        
     
      }
        }
    onDeleteUser(event): void {
      
      if (window.confirm('Are you sure you want to delete?')) {
        this.http.delete<any>('http://localhost:3000/rfq/users/'+event.data._id).subscribe(result=>{});
  
        event.confirm.resolve();
      } else {
        event.confirm.reject();
      }
    
    }
    toAddUser(){
      this.router.navigate(['pages/Security-managment-elements/addUser']);
    }
    
    init(colors: any) {
      this.Settings = [
        {
          status: 'primary',
          container: 'primary-container',
          title: 'Primary Button',
          buttonTitle: 'Add User',
          default: {
            gradientLeft: `adjust-hue(${colors.primary}, 20deg)`,
            gradientRight: colors.primary,
          },
          corporate: {
            color: colors.primary,
            glow: {
              params: '0 0 20px 0',
              color: 'rgba (115, 161, 255, 0.5)',
            },
          },
          cosmic: {
            gradientLeft: `adjust-hue(${colors.primary}, 20deg)`,
            gradientRight: colors.primary,
            bevel: `shade(${colors.primary}, 14%)`,
            shadow: 'rgba (6, 7, 64, 0.5)',
            glow: {
              params: '0 2px 12px 0',
              color: `adjust-hue(${colors.primary}, 10deg)`,
            },
          },
        },
     
      ];
      
    }  
    
  }
  
  