import { NbThemeService, NbDateService, NbDialogService } from "@nebular/theme";
import { Component, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { AuthenticationService } from "../../../auth/Auth.Service";
import { ShowcaseDialogComponent } from "../../modal-overlays/dialog/showcase-dialog/showcase-dialog.component";

@Component({
    selector: 'ngx-add-user',
    styleUrls: ['./add-user.component.scss'],
    templateUrl: './add-user.component.html',
  })
  export class AddUserComponent implements OnInit  {
    /*validation_messages= {
      'username': [
        { type: 'required', message: 'Username is required' },
        { type: 'minlength', message: 'Username must be at least 5 characters long' },
        { type: 'maxlength', message: 'Username cannot be more than 25 characters long' },
        { type: 'pattern', message: 'Your username must contain only numbers and letters' },
        { type: 'validUsername', message: 'Your username has already been taken' }
      ],
      'password': [
        { type: 'required', message: 'Password is required' },
        { type: 'minlength', message: 'Password must be at least 5 characters long' },
        { type: 'pattern', message: 'Your password must contain at least one uppercase, one lowercase, and one number' }
      ]}*/

    AddUserForme: FormGroup;
    usenameMessages:any;
cofirmedPass:boolean=false;
cofirmePass:boolean=true;
  constructor(private router:Router,private http:HttpClient,private fb: FormBuilder,private authService:AuthenticationService,private dialogService:NbDialogService ){ 
    
    if(this.authService.Role['subject']!='admin'){ this.dialogService.open(ShowcaseDialogComponent, {
      context: {
        title: 'User not permetted',
        message:'this section is not a part of your role !!',
        
      },
    });
  this.router.navigate(['pages']);}
  }
  ngOnInit(): void {
   
    this.AddUserForme = this.fb.group({
      userName: ['', Validators.required],
      // ,Validators.minLength(5),Validators.maxLength(25),Validators.pattern('^[a-zA-Z0-9_.+-]+.[a-zA-Z0-9-.]+$')],
      password: ['', Validators.required] ,
      //Validators.minLength(8),
      //Validators.pattern('(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[$@$!%*?&])[A-Za-z\d$@$!%*?&].{8,}')],
      cofirmpassword: ['', Validators.required ],
      role: ['', Validators.required ]
    });
    this.onChanges()
  
}
  AddUser(userData){

if(this.AddUserForme.get('password').value==this.AddUserForme.get('cofirmpassword').value){




   this.http.post<any>('http://localhost:3000/rfq/users',userData).subscribe(result=>{
    if(result.message==null){this.router.navigate(['pages/Security-managment-elements/Users-table']);
    this.dialogService.open(ShowcaseDialogComponent, {
      context: {
        
        message:'User Saved successsfully ',
      },
    });}
    else if (result.message!=null)

    {this.usenameMessages=result.message;}
   });

 }
 
 


}

onChanges():void{
  this.AddUserForme.get('userName').valueChanges.subscribe(val => {
    this.usenameMessages = null;
  });
  this.AddUserForme.get('cofirmpassword').valueChanges.subscribe(val => {
    if(this.AddUserForme.get('password').value!=this.AddUserForme.get('cofirmpassword').value){
      this.cofirmePass=false; 
      this.cofirmedPass=false;  
    }
    else {this.cofirmedPass=true;
    this.cofirmePass=true;}
  });
}

}


